
.bigCard{
    width: 80%;
    height: 85%;
    border-radius: 15px;
    outline: 5px solid black;
    margin: 30px;
    background: linear-gradient(-45deg, #000000, #000000c5, #351f11e8, #3d210a);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    display: flex;
    margin: 0 auto;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}


#bigCardImg{
    margin-top: 2rem;
    height:40%;
    width:90%;
    outline: 5px solid black;
    border-radius: 15px;
    object-fit: cover;
}
#gameTitle{
    padding-right: 5rem;
}
@keyframes gradient {
    0% {

        opacity: 0;
        background-position: 0% 50%;
    }
    1% {
        opacity: 1;
        background-position: 100% 50%;
    }
    100% {
        
        opacity: 1;
        background-position: 0% 50%;
    }
}
#gameDesc{
 padding-left: 2rem;
 text-align: center;
 width: fit-content;
 height: auto;
 height: px;
 display: inline-block;
 font-size: 1em;
}

#descDiv{
    display: inline-block;
}

#linkDiv{

}

.col{
    width: 33.33%;
    padding: 10px;
    max-height: 100%;
  }

#links{
    padding-top: 1rem;
    text-align: center;
}
#stars{
    text-align: center;
    position: absolute;
    bottom: 0;
}
.btnClass{
    border: none;
    background: none;
    transition-duration: .25s;
    margin:0em 1em;

}

.btnClass:hover{
    transform: scale(1.5);
}

.screenshots{
    margin:0.2em;
    border-radius: 15px;
    object-fit: cover;
}
.popupScreenshot{
    position: fixed;
    right:25%;
    bottom:25%;
    border-radius: 50px;
    outline: 5px solid rgba(179, 109, 44, 0.466);

}
.popupBigImg{
    position: fixed;
    right:20%;
    bottom:20%;
    border-radius: 50px;
    outline: 5px solid rgba(0, 0, 0, 0.466);

}